@font-face {
  font-family: "GolosText";
  src: url("../fonts/GolosText-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "GolosText";
  src: url("../fonts/GolosText-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "GolosText";
  src: url("../fonts/GolosText-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "GolosText";
  src: url("../fonts/GolosText-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Merriweather";
  src: url("../fonts/Merriweather-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: "GolosText", sans-serif;
}

a {
  text-decoration: none;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.text-red {
  color: #F54932;
  font-weight: 700;
}

.text-blue {
  color: #3057A2;
}

.clear-both {
  clear: both;
}

.container {
  max-width: 1262px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 2px solid transparent;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  line-height: 120%;
  letter-spacing: 0px;
  transition: all 0.2s;
  font-family: "GolosText", sans-serif;
}
.btn-long {
  gap: 4px;
  border-radius: 66px;
  padding: 18px 20px;
  line-height: 130%;
  width: 100%;
  height: 47px;
}
@media screen and (min-width: 1251px) {
  .btn-long {
    flex-direction: column;
    gap: 0;
    padding: 20px;
    width: 263px;
    height: 72px;
  }
}
.btn-short {
  padding: 12x;
  border-radius: 62px;
  width: 113px;
  height: 35px;
}
.btn-square {
  width: 44px;
  height: 44px;
  border-radius: 50%;
}
.btn-dark {
  background-color: #1F1F1F;
  border-color: #1F1F1F;
  color: #FFF;
}
.btn-dark:hover {
  background-color: #FBCE51;
  border-color: #FBCE51;
  color: #1F1F1F;
}
.btn-outline-dark {
  border-color: #1F1F1F;
  color: #1F1F1F;
}
.btn-outline-dark:hover {
  background-color: #1F1F1F;
  border-color: #1F1F1F;
  color: #FFF;
}
.btn-outline-blue {
  border-color: #3057A2;
  color: #3057A2;
}
.btn-outline-blue:hover {
  background-color: #3057A2;
  border-color: #3057A2;
  color: #FFF;
}
.btn:disabled {
  background-color: #D6D6D6;
  border-color: #D6D6D6;
  cursor: auto;
  pointer-events: none;
}

