  top: 0;
}
@media screen and (min-width: 1251px) {
  .intro__bg-pawn {
    width: 82px;
    height: 122px;
    position: absolute;
    z-index: 5;
    background-image: url("/static/img/firstscreen/pawn.png");
    background-size: contain;
    background-repeat: no-repeat;
    bottom: 10px;
    left: 90%;
  }
}
.intro__bg-horse {
  width: 121px;
  height: 121px;
  position: absolute;
  z-index: 5;
  background-image: url("/static/img/firstscreen/horse.png");
  background-size: contain;
  background-repeat: no-repeat;
  bottom: -4%;
  left: 36%;
}
@media screen and (min-width: 376px) {
  .intro__bg-horse {
    left: 50%;
  }
}
@media screen and (min-width: 1251px) {
  .intro__bg-horse {
    width: 154px;
    height: 244px;
    bottom: -10%;
    left: 63%;
  }
