.running-line {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  width: 100%;
  background-color: #F54932;
  padding: 18px 12px;
  height: 60px;
  position: relative;
  z-index: 10;
}
.running-line__container {
  animation: scroll 20s linear infinite;
  float: left;
  white-space: nowrap;
  display: flex;
}
.running-line__group {
  display: flex;
  flex-shrink: 0;
}
.running-line__item {
  color: #FFF;
  flex: 0 0 auto;
  margin-right: 18px;
  margin-left: 12px;
  font-family: "Merriweather", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 110%;
  text-transform: uppercase;
  position: relative;
}
.running-line__item::after {
  content: "";
  position: absolute;
  right: -18px;
  top: 50%;
  transform: translateY(-50%);
  width: 6px;
  height: 6px;
  background-color: #FFF;
  border-radius: 50%;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.slider {
  display: flex;
  gap: 20px;
  transition: transform 0.5s ease;
}
.slider__header {
  color: #1F1F1F;
  font-family: "Merriweather", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 110%;
  text-transform: uppercase;
}
@media screen and (min-width: 1251px) {
  .slider__header {
    font-size: 54px;
  }
}
.slider-container {
  overflow: hidden;
  position: relative;
}
.slider__item {
  flex-shrink: 0;
}
.slider__pagination-dots {
  height: 10px;
  display: flex;
  gap: 10px;
}
.slider__pagination-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #D6D6D6;
}
.slider__pagination-dot.active {
  background-color: #1F1F1F;
}
.slider__controls {
  display: flex;
  gap: 14px;
  align-items: center;
  justify-content: center;
}
.slider__button {
  flex-shrink: 0;
}
.slider__button--prev img {
  transform: scaleX(-1);
}

